<template>
    <div class="container">
        <v-back title="考勤结果详情" :show="false" />
        <div class="from">
            <md-field>
				<div class="banner flex space-between">推送日期 <div>{{$route.query.time}}[{{$route.query.stage == 'am' ? '早' : '晚'}}]</div></div>
				<div class="banner flex space-between">宿舍楼 <div>{{buildingName}}</div></div>
            </md-field>
        </div>


        <div class="list flex space-between">
            <div class="list_one bgcolor-1" @click="toList(0, num[0].num)">
                <div class="flex space-between">
                    <md-icon name="home" size="lg"></md-icon>
                    <div>
                        <p>{{num[0].num || 0}}</p>
                        <div class="title">考勤人数</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-2" @click="toList(1, num[1].num)">
                <div class="flex space-between">
                    <md-icon name="share" size="lg"></md-icon>
                    <div>
                        <p>{{num[1].num || 0}}</p>
                        <div class="title">身份未登记人数</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-3" @click="toList(2, num[2].num)">
                <div class="flex space-between">
                    <md-icon name="back" size="lg"></md-icon>
                    <div>
                        <p>{{num[2].num || 0}}</p>
                        <div class="title">今日通行人次</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-4" @click="toList(3, num[3].num)">
                <div class="flex space-between">
                    <md-icon name="calendar" size="lg"></md-icon>
                    <div>
                        <p>{{num[3].num || 0}}</p>
                        <div class="title">24小时以上未归寝人员</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-5" @click="toList(4, num[4].num)">
                <div class="flex space-between">
                    <md-icon name="switch" size="lg"></md-icon>
                    <div>
                        <p>{{num[4].num || 0}}</p>
                        <div class="title">24小时以上未出寝人员</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-6" @click="toList(5, num[5].num)">
                <div class="flex space-between">
                    <md-icon name="clock" size="lg"></md-icon>
                    <div>
                        <p>{{num[5].num || 0}}</p>
                        <div class="title">闭寝时间未归人员</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-7" @click="toList(6, num[6].num)">
                <div class="flex space-between">
                    <md-icon name="authentication" size="lg"></md-icon>
                    <div>
                        <p>{{num[6].num || 0}}</p>
                        <div class="title">闭寝后晚归人员</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-8" @click="toList(7, num[7].num)">
                <div class="flex space-between">
                    <md-icon name="motor-vehicle" size="lg"></md-icon>
                    <div>
                        <p>{{num[7].num || 0}}</p>
                        <div class="title">闭寝后早出人员</div>
                    </div>
                </div>
            </div>
        </div>
		<div class="home_btn" @click="toHome">进入系统首页</div>
    </div>
</template>

<script>
    import dayjs from 'dayjs'
	export default {
		name: 'report_index',
		data() {
			return { 
                data2: [],
				buildingName: '',
                num: [{type: 0, num: 0},{type: 1, num: 0},{type: 2, num: 0},{type: 3, num: 0},{type: 4, num: 0},{type: 5, num: 0},{type: 6, num: 0},{type: 7, num: 0},]
            }
        },
        methods: {
			getFloorName() {
				this.data2.forEach(item => {
					if (item.value == this.$route.query.buildingId) {
						this.buildingName =  item.text
					}
				})
			},
            //获取宿舍楼栋
            getFloor() {
                this.$getL('/dorm/floor', {
					userId: JSON.parse(localStorage.getItem('user')).user_id
				}, (res)=>{
                    this.data2 = []
                    res.data.forEach((item, index) => {
                        this.data2.push({
                            value: item.buildingId,
                            text: item.buildingName
                        })
                    })
					this.getFloorName()
					setTimeout(() => {
						this.getData()
					}, 200)
                    
				})
            },
            toList(type, total) {
                this.$router.push('/notice_detail/list?type=' + type + '&time=' + this.$route.query.time + '&buildingId=' + this.$route.query.buildingId + '&buildingName=' + this.buildingName + '&total=' + total + '&stage=' + this.$route.query.stage)
            },
            getData() {
                this.$getL('/notice/list_new', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id,
                    time: this.$route.query.time,
                    buildingId: this.$route.query.buildingId,
					stage: this.$route.query.stage
                }, (res)=>{
                    this.$toast.hide()
                    this.num = res.data
                })
            },
			toHome() {
				this.$router.push('/home')
			}
        },
        mounted() {
            this.getFloor()
        }
    }
</script>

<style scoped>
    .container {
        height: 100vh;
        overflow-y: auto;
    }
    .list {
        font-size: 4vw ;
        flex-wrap: wrap;
        padding: 2vw;
    }
    .list_one {
        box-sizing: border-box;
        width: calc(50% - 1vw);
        color: #fff;
        padding: 2vw;
        margin-bottom: 2vw;
        height: 20vw;
		border-radius: 1.5vw;
		box-shadow: 2px 2px 5px rgba(0,0,0,.2);
    }
    .list_one .md-icon {
        font-size: 7vw;
        opacity: .8;
    }
    .list_one p {
        font-size: 6vw;
        text-align: right;
        margin-bottom: 3vw;
    }
    .title {
        margin-left: -40vw;
        text-align: right;
        font-size: 3.5vw;
    }
	.banner {
		font-size: 4vw;
		padding: 1vw 0;
	}
	.banner div {
		font-weight: bolder;
	}
	.home_btn {
		box-sizing: border-box;
		color: #fff;
		background-color: #1acfc6;
		font-size: 3.5vw;
		padding: 2vw;
		border-radius: 4vw;
		text-align: center;
		margin: 5vw;
		box-shadow: 2px 2px 5px rgba(0,0,0,.2);
	}
</style>